.mountainHeader {
    /* @include adapt-value('padding-top', 25, 5, 1); */
    overflow        : hidden;
    position        : relative;
    background-color: white;
    transition      : 0.3s all;






    &__title {
       




        
       
    }

    

   
}


.title_mountains {
    /* overflow        : visible; */
    background-color: white;

    z-index : 4;
    position: relative;




    font-family: "Open Sans";

    &::before {




        @extend %runLine_top;


    }

    &::after {



        background-image: url('../../../content/images/TitleMountains/Bottom_.png');

        @extend %runLine_bottom;


    }




    &__body {
        position   : relative;
        z-index    : 10;
        user-select: none;


        @include adapt-value('padding-top', 300, 40, 0);
        @include adapt-value('padding-bottom', 150, 65, 0);

        @media (max-width:767px) {
            @include adapt-value('padding-top', 150, 65, 0);
            @include adapt-value('padding-bottom', 150, 65, 0);
        }

        position: relative;

        @include adapt-value('min-height', 768, 300, 0);
        display       : flex;
        flex-direction: column;

        &::before {
            content   : '';
            position  : absolute;
            z-index: -1;
            left      : 0;
            top       : 0;
            width     : 100%;
            height    : 100%;
            background: url('../../../content/images/TitleMountains/Left_.png') 0% 50%/ 45% 65% no-repeat;
            @extend %increaseAnimation;
            @media (max-width:1200px) {
                background: url('../../../content/images/TitleMountains/Left_.png') 0% 50%/ 75vw 50vw no-repeat;
            }
        }
    }

    &__container {
        padding    : 0 15px;
        flex-grow  : 1;
        margin     : 0 auto;
        max-width  : 1050px;
        display    : flex;
        align-items: center;
    }

   & &__image_gallery {
        position: absolute;
        z-index : 1;
        top     : 0;
        left    : 0;
        width   : 100%;
        height  : 100%;

        &::before,
        &::after {
            content : '';
            top     : 0;
            left    : 0;
            width   : 100%;
            height  : 100%;
            position: absolute;
            z-index : 3;
        }

        &::before {

            background-color: hsla(36, 3%, 32%, 0.5);

        }

        &::after {




            background-color: hsla(0%, 0%, 0%, 0.3);

            @media (max-width:577px) {

                background-color: hsla(0%, 0%, 0%, 0.6);

            }
        }


        &>div {

            width : 100%;
            height: 100%;

        }

        &>div>div {

            width : 100%;
            height: 100%;
        }

        &>div>div>div {

            width : 100%;
            height: 100%;
        }

        &>div>div>div>div {

            width : 100%;
            height: 100%;
        }

        &>div>div>div>div>div {

            width : 100%;
            height: 100%;
        }

        &>div>div>div>div>div>img {
            object-fit: cover;
            position  : absolute;
            top       : 0;
            left      : 0;
            width     : 100%;
            height    : 100%;
            min-height: 100%;

        }




    }







}

._Dark {
    .title_mountains {

        transition: 0.3s all;

        &::before {
            transition: inherit;




            opacity: 0.7;

        }

        &::after {
            transition: inherit;
            opacity   : 0.7;




        }
    }


}

._Dark.mountainHeader {
    padding-top: 0;
}



.content {

    &__body {
        text-align : center;
        line-height: 1.2;

        div:last-child {

            max-width: 770px
        }

    }

    &__title {
        @include adapt-value('margin-bottom', 60, 20, 0);

        &>p {


            @include adapt-value('font-size', 58.275, 24, 0);
            color: rgb(255, 255, 255);

        }

    }

    &__sub_title {

        &>p {

            hyphens        : auto;
            -moz-hyphens   : auto;
            -webkit-hyphens: auto;
            -ms-hyphens    : auto;

            display: block;
            padding: 0 5px;


            @include adapt-value('font-size', 29, 14.5, 0);

            color: rgb(219, 219, 219);

            @media (max-width:577px) {
                color: white;


            }

        }

        @include adapt-value(margin-bottom, 80, 21, 0);




    }
}